import React, { useState } from 'react';
import { Box, List, ListItem, ListItemText, Typography, Button, Pagination } from '@mui/material';
import CarIcon from '@mui/icons-material/DirectionsCar';
import PersonIcon from '@mui/icons-material/Person';
import { LockClock } from '@mui/icons-material';

// Simulao dos dados de pedidos de aluguel
const mockAlugueis = [
  {
    id: 1,
    automovel: { modelo: 'Fusca', marca: 'Volkswagen', ano: 1975, placa: 'ABC-1234', disponivel: true, proprietario: { nome: 'Joo Silva', profissao: 'Engenheiro', endereco: 'Rua A', senha: '1234', tipoUsuario: 'proprietario' }},
    cliente: { nome: 'Maria Souza', profissao: 'Advogada', endereco: 'Rua B', senha: 'abcd', tipoUsuario: 'cliente' },
    diasAluguel: 5,
    aderido: 'PENDENTE'
  },
  {
    id: 2,
    automovel: { modelo: 'Corolla', marca: 'Toyota', ano: 2019, placa: 'DEF-5678', disponivel: true, proprietario: { nome: 'Carlos Pereira', profissao: 'Professor', endereco: 'Rua C', senha: '4321', tipoUsuario: 'proprietario' }},
    cliente: { nome: 'Ana Lima', profissao: 'Mdica', endereco: 'Rua D', senha: 'efgh', tipoUsuario: 'cliente' },
    diasAluguel: 7,
    aderido: 'PENDENTE'
  },
];

const OrderPage: React.FC = () => {
  const [alugueis, setAlugueis] = useState(mockAlugueis); // Dados simulados
  const [page, setPage] = useState(1);
  const rowsPerPage = 2;

  const handleChange = (event: React.ChangeEvent<unknown>, value: number) => {
    setPage(value);
  };

  const handleAderir = (id: number) => {
    const updatedOrders = alugueis.map(order =>
      order.id === id ? { ...order, aderido: "ADERIDO" } : order
    );
    setAlugueis(updatedOrders);
  };

  const handleRecusar = (id: number) => {
    const updatedOrders = alugueis.map(order =>
      order.id === id ? { ...order, aderido: "RECUSADO" } : order
    );
    setAlugueis(updatedOrders);
  };

  // Clculo da pgina atual
  const paginatedAlugueis = alugueis.slice((page - 1) * rowsPerPage, page * rowsPerPage);

  // Mapeamento das cores com base na situao
  const statusColors: { [key: string]: string } = {
    PENDENTE: 'orange',
    ADERIDO: 'green',
    RECUSADO: 'red',
  };

  return (
    <Box sx={{ padding: '20px' }}>
      <Typography variant="h4" gutterBottom>
        Pedidos de Aluguel
      </Typography>
      <List>
        {paginatedAlugueis.length > 0 ? (
          paginatedAlugueis.map((car) => (
            <ListItem
              key={car.id}
              sx={{
                border: '2px solid #1976d2',
                borderRadius: '8px',
                padding: '20px',
                marginBottom: '15px',
                backgroundColor: '#f9f9f9',
                boxShadow: 3,
                display: 'flex',
                alignItems: 'center',
              }}
            >
              <CarIcon sx={{ fontSize: 40, marginRight: 2 }} />
              <ListItemText
                primary={`${car.automovel.modelo.toUpperCase()}`}
                secondary={
                  <Box>
                    <Typography>
                      <PersonIcon sx={{ verticalAlign: 'middle', marginRight: 1 }} />
                      Cliente: {car.cliente.nome}
                    </Typography>
                    <Typography>
                      <LockClock sx={{ verticalAlign: 'middle', marginRight: 1 }} />
                      Dias de Aluguel: {car.diasAluguel}
                    </Typography>
                    <Typography sx={{ color: statusColors[car.aderido] || 'black' }}>
                      Situao: {car.aderido}
                    </Typography>
                    <Box sx={{ marginTop: 1 }}>
                      <Button 
                        variant="contained" 
                        color="success" 
                        onClick={() => handleAderir(car.id)} 
                        disabled={car.aderido !== "PENDENTE"}
                      >
                        Aderir
                      </Button>
                      <Button 
                        variant="contained" 
                        color="error" 
                        onClick={() => handleRecusar(car.id)} 
                        sx={{ marginLeft: 1 }} 
                        disabled={car.aderido !== "PENDENTE"}
                      >
                        Rejeitar
                      </Button>
                    </Box>
                  </Box>
                }
              />
            </ListItem>
          ))
        ) : (
          <Typography>Nenhum pedido encontrado.</Typography>
        )}
      </List>
      {/* Paginao */}
      <Box sx={{ display: 'flex', justifyContent: 'center', marginTop: '20px' }}>
        <Pagination
          count={Math.ceil(alugueis.length / rowsPerPage)}
          page={page}
          onChange={handleChange}
          color="primary"
        />
      </Box>
    </Box>
  );
};

export default OrderPage;
