import React, { useState } from 'react';
import { Box, List, ListItem, ListItemText, Typography } from '@mui/material';
import CarIcon from '@mui/icons-material/DirectionsCar';
import PersonIcon from '@mui/icons-material/Person';
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack';
import { LockClock, Verified } from '@mui/icons-material';

// Aluguel local
export type Aluguel = {
  id: number;
  automovel: {
    modelo: string;
    marca: string;
    ano: number;
    placa: string;
    disponivel: boolean;
    proprietario: {
      nome: string;
      profissao: string;
      endereco: string;
      senha: string;
      tipoUsuario: string;
    };
  };
  cliente: {
    nome: string;
    profissao: string;
    endereco: string;
    senha: string;
    tipoUsuario: string;
  };
  proprietario: {
    nome: string;
    profissao: string;
    endereco: string;
    senha: string;
    tipoUsuario: string;
  };
  dataAluguel: Date;
  diasAluguel: number;
  aderido: string;
};

export let alugueis: Aluguel[] = [];

// tipo Car localmente
export type Car = {
  modelo: string;
  marca: string;
  ano: number;
  placa: string;
  disponivel: boolean;
  proprietario: {
    nome: string;
    profissao: string;
    endereco: string;
    senha: string;
    tipoUsuario: string;
  };
};


export let carros: Car[] = [];


// Simulao dos dados
const mockAlugueis: Aluguel[] = [
  {
    id: 1,
    automovel: { modelo: 'Fusca', marca: 'Volkswagen', ano: 1975, placa: 'ABC-1234', disponivel: true, proprietario: { nome: 'Joo Silva', profissao: 'Engenheiro', endereco: 'Rua A', senha: '1234', tipoUsuario: 'cliente' }},
    cliente: { nome: 'Maria Souza', profissao: 'Advogada', endereco: 'Rua B', senha: 'abcd', tipoUsuario: 'cliente' },
    proprietario: { nome: 'Joo Silva', profissao: 'Engenheiro', endereco: 'Rua A', senha: '1234', tipoUsuario: 'cliente' },
    dataAluguel: new Date(),
    diasAluguel: 5,
    aderido: 'PENDENTE'
  },
  {
    id: 2,
    automovel: { modelo: 'Corolla', marca: 'Toyota', ano: 2019, placa: 'DEF-5678', disponivel: true, proprietario: { nome: 'Carlos Pereira', profissao: 'Professor', endereco: 'Rua C', senha: '4321', tipoUsuario: 'cliente' }},
    cliente: { nome: 'Ana Lima', profissao: 'Mdica', endereco: 'Rua D', senha: 'efgh', tipoUsuario: 'cliente' },
    proprietario: { nome: 'Carlos Pereira', profissao: 'Professor', endereco: 'Rua C', senha: '4321', tipoUsuario: 'cliente' },
    dataAluguel: new Date(),
    diasAluguel: 7,
    aderido: 'ADERIDO'
  },
  {
    id: 3,
    automovel: { modelo: 'Civic', marca: 'Honda', ano: 2020, placa: 'GHI-9876', disponivel: true, proprietario: { nome: 'Pedro Santos', profissao: 'Designer', endereco: 'Rua E', senha: 'pass123', tipoUsuario: 'cliente' }},
    cliente: { nome: 'Lucas Martins', profissao: 'Engenheiro Civil', endereco: 'Rua F', senha: 'secure456', tipoUsuario: 'cliente' },
    proprietario: { nome: 'Pedro Santos', profissao: 'Designer', endereco: 'Rua E', senha: 'pass123', tipoUsuario: 'cliente' },
    dataAluguel: new Date(),
    diasAluguel: 3,
    aderido: 'RECUSADO'
  }
];

const MyCarsPage: React.FC = () => {
  const [alugueis] = useState(mockAlugueis); // Dados simulados
  const [page, setPage] = useState(1);
  const rowsPerPage = 2;

  const handleChange = (event: React.ChangeEvent<unknown>, value: number) => {
    setPage(value);
  };

  // Clculo da pgina atual
  const paginatedAlugueis = alugueis.slice((page - 1) * rowsPerPage, page * rowsPerPage);

  // Mapeamento das cores com base na situao
  const statusColors: { [key: string]: string } = {
    PENDENTE: 'orange',
    ADERIDO: 'green',
    RECUSADO: 'red',
  };

  return (
    <Box sx={{ padding: '20px' }}>
      <Typography variant="h4" gutterBottom>
        Meus Aluguis
      </Typography>
      <List>
        {paginatedAlugueis.length > 0 ? (
          paginatedAlugueis.map((car) => (
            <ListItem
              key={car.id}
              sx={{
                border: '2px solid #1976d2',
                borderRadius: '8px',
                padding: '20px',
                marginBottom: '15px',
                backgroundColor: '#f9f9f9',
                boxShadow: 3,
                display: 'flex',
                alignItems: 'center',
              }}
            >
              <CarIcon sx={{ fontSize: 40, marginRight: 2 }} />
              <ListItemText
                primary={`${car.automovel.modelo.toUpperCase()}`}
                secondary={
                  <Box>
                    <Typography>
                      <PersonIcon sx={{ verticalAlign: 'middle', marginRight: 1, marginBottom: 1, marginTop: 1 }} />
                      Proprietrio: {car.proprietario.nome}
                    </Typography>
                    <Typography>
                      <CalendarTodayIcon sx={{ verticalAlign: 'middle', marginRight: 1, marginBottom: 1 }} />
                      Data do Aluguel: {new Date(car.dataAluguel).toLocaleDateString()}
                    </Typography>
                    <Typography>
                      <LockClock sx={{ verticalAlign: 'middle', marginRight: 1, marginBottom: 1 }} />
                      Dias de Aluguel: {car.diasAluguel}
                    </Typography>
                    <Typography
                      sx={{ color: statusColors[car.aderido] || 'black' }} // Aplique a cor da situao
                    >
                      <Verified sx={{ verticalAlign: 'middle', marginRight: 1, marginBottom: 1 }} />
                      Situao: {car.aderido}
                    </Typography>
                  </Box>
                }
              />
            </ListItem>
          ))
        ) : (
          <Typography>Nenhum aluguel encontrado.</Typography>
        )}
      </List>
      {/* Paginao */}
      <Stack spacing={2} sx={{ position: 'fixed', bottom: 20, right: 20 }}>
        <Pagination
          count={Math.ceil(alugueis.length / rowsPerPage)}
          page={page}
          onChange={handleChange}
          color="primary"
        />
      </Stack>
    </Box>
  );
};

export default MyCarsPage;
