import React, { useState } from 'react';
import { Box, Button, Typography } from '@mui/material';
import { CarRental, DateRange, Star } from '@mui/icons-material'; // Importando cones

// Definio de uma lista de carros simulada
const mockCarros = [
  {
    placa: 'ABC1234',
    modelo: 'Ford Mustang',
    marca: 'Ford',
    ano: 2021,
  },
  {
    placa: 'XYZ5678',
    modelo: 'Tesla Model 3',
    marca: 'Tesla',
    ano: 2022,
  },
];

const RentPage: React.FC = () => {
  const [carros] = useState(mockCarros);

  const handleRequest = (vehicle: any) => {
    alert(`Solicitao para alugar o ${vehicle.modelo} enviada!`);
  };

  return (
    <Box
      display="flex"
      flexDirection="column"
      alignItems="center"
      sx={{ minHeight: '80vh', padding: 3, backgroundColor: '#f5f5f5' }}
    >
      <Box display="flex" flexWrap="wrap" justifyContent="center" gap={3}>
        {carros.length > 0 ? (
          carros.map((vehicle) => (
            <Box
              key={vehicle.placa}
              sx={{
                width: '300px',
                padding: '20px',
                border: '2px solid #191970',
                borderRadius: '12px',
                textAlign: 'center',
                boxShadow: '0 4px 10px rgba(0, 0, 0, 0.1)',
                backgroundColor: '#fff',
                transition: 'transform 0.2s',
                '&:hover': {
                  transform: 'scale(1.05)',
                  boxShadow: '0 8px 20px rgba(0, 0, 0, 0.2)',
                },
              }}
            >
              <CarRental sx={{ fontSize: 50, color: '#191970' }} />
              <Typography variant="h5" gutterBottom>
                {vehicle.modelo}
              </Typography>
              <Typography variant="body1" color="textSecondary">
                {vehicle.marca} - {vehicle.ano}
              </Typography>
              <Typography
                variant="body2"
                sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', marginTop: 1 }}
              >
                <Star sx={{ marginRight: 0.5 }} />
                4.5
              </Typography>
              <Button
                variant="contained"
                onClick={() => handleRequest(vehicle)}
                sx={{
                  backgroundColor: '#191970',
                  color: '#fff',
                  '&:hover': {
                    backgroundColor: '#fff',
                    color: '#191970',
                  },
                  marginTop: 2,
                }}
              >
                Enviar Solicitao
                <DateRange sx={{ marginLeft: 1 }} />
              </Button>
            </Box>
          ))
        ) : (
          <Typography variant="h6">Carros no disponveis</Typography>
        )}
      </Box>
    </Box>
  );
};

export default RentPage;
